@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@400;500;600;700&display=swap') ;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

main {
  background-image: url('./images/background.png');
  color: white;
  font-family: Epilogue , sans-serif;
  text-align: center;
}

header {
  font-size: 50px;
  font-weight: 600;
  padding: 50px 0;
}

h2 {
  background-color: rgba(24, 26, 37, 0.5);
  border: 1px solid white;
  border-radius: 17px;
  font-size: 40px;
  font-weight: 500;
  margin: 0 40px;
  padding: 20px 0;
}

.planets-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 70px 0;
}

.planet-card {
  align-items: center;
  display: flex;
  flex-direction: column-reverse;
  font-weight: 400;
  height: 220px;
  justify-content: space-between;
  width: 185px;
}

.planet-card img {
  width: 160px;
}

.planet-card:first-child img {
  height: 31px;
  margin-top: 75px;
  width: 41px;
}

.planet-card:nth-child( 2 ) img {
  height: 76px;
  margin-top: 50px;
  width: 102px;
}

.planet-card:nth-child( 3 ) img {
  height: 79px;
  margin-top: 50px;
  width: 106px;
}

.planet-card:nth-child( 4 ) img {
  height: 50px;
  margin-top: 70px;
  width: 66px;
}

.planet-card:nth-child( 5 ) img {
  width: 220px;

  /* height: 204px; */

  /* margin-top: 37px; */
}

.planet-card:nth-child( 6 ) img {
  width: 200px;

  /* height: 204px; */

  /* margin-top: 37px; */
}

.planet-card:nth-child( 7 ) img {

  /* height: 204px; */
  margin-top: 37px;
  width: 120px;
}

.planet-card:nth-child( 8 ) img {

  /* height: 204px; */
  margin-top: 37px;
  width: 120px;
}

.mission-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 40px;
  padding: 50px 0 150px;
}

.mission-card {
  background-color: rgba(24, 26, 37, 0.5);
  border: 1px solid white;
  border-radius: 17px;
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 20px;
  padding: 30px 0;
  text-align: left;
  width: 345px;
}

.mission-card p {
  margin: 15px 0 0 30px;
}

.mission-card p:first-child {
  border-bottom: 1px solid white;
  font-size: 25px;
  font-weight: 700;
  padding-bottom: 20px;
}
